import React, { useState } from 'react';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

const scrollMenu = (newValue = 0) =>{
  let list = document.querySelectorAll(".menuItems .itemsList > li")
  //console.log(list[1].getBoundingClientRect().top)
  //console.log(list[1].offsetTop)
  window.scrollTo({
    top:list[newValue].offsetTop - 201
  })
  //console.log(this , scroll, list.length)
  // document.querySelector("#menu_03").scrollTo(0, 0);
  // console.log(document.querySelector("#menu_03"))

}

export default function ListTabMenu(props) {
  //console.log(props.sendProps)
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
    //props.dta(newValue)
    scrollMenu(newValue)
    //setMenuRef(1)
  };
  return (
    <div className="menuListTab pageTabMenu  withMaterial">
        <Tabs
            value={value}
            onChange={handleChange}
            variant="scrollable"
            scrollButtons="auto"
            aria-label=""
            indicatorColor="primary"
        >
            <Tab label="대표메뉴" />
            <Tab label="SET메뉴" />
            <Tab label="주류" />
            <Tab label="커피" />
            <Tab label="주스" />
            <Tab label="에이드" />
            <Tab label="음료" />
        </Tabs>
    </div>
  );
}