
import axios from 'axios'
import {objectToQueryString,getAccessId} from './util/Utils'

axios.defaults.baseURL = 'http://192.168.23.130:8080/api/v1'
// axios.defaults.baseURL = 'http://192.168.23.97:8080/sdl/api/v1'
// axios.defaults.mode = 'cors'
// axios.defaults.credentials = 'include'

let config = {
    withCredentials : true,
    timeout: 1000,
    headers: {
        'Content-Type': 'application/json',
        'Accept' : '*/*'
    }
}

axios.interceptors.request.use(function (config) {
    const token = localStorage.getItem('accessId');
    config.headers.Authorization =  token ? `Bearer ${token}` : '';
    return config;
  });


export async function getNewStores() {
    
    const response = await axios.get(
        'boards/new-stores', config
    );
    return response.data;
}

export async function getCatogories() {
    const response = await axios.get(
        'stores-category', config
    );
    return response.data;
}

export async function getStores(lat, lng, bizCtgDtl) {
    const params = {userLat : lat, userLng : lng, bizCtgDtl : bizCtgDtl};
    const url = "stores?" + objectToQueryString(params) ;
    const response = await axios.get(
         url, config
    );
    return response.data;
}

export async function getArround(lat, lng) {
    const params = {userLat: lat, userLng: lng};
    const url = "search/stores?" + objectToQueryString(params);
    
    const response = await axios.get(
        url, config
    );
    return response.data;
}

export async function getSearch(lat, lng, value) {
    const params = {userLat: lat, userLng: lng, searchValue: value};
    const url = "search/keywords?" + objectToQueryString(params);
    
    const response = await axios.get(
        url,config
    );
    return response.data;
}


export async function getStoresMenus(strId) {
    const params = {strId: strId};
    const url = "search/keywords?" + objectToQueryString(params);
    
    const response = await axios.get(
        url, config
    );
    return response.data;
}

export async function signIn(id, pass, keep) {
    const url = 'members/login';
    const response = await axios.post(
        url,
        {
            mbrId: id,
            mbrPwd: pass,
            isKeepLogin: keep
        },
        config
    );
    return response.data
}

/**
 * 로그아웃
 */
export async function logOut() {
    const url = 'members/logout';
    const response = await axios.get(
        url,
        config
    );
    return response.data
}

export async function anonymous(appVersion,os,pushToken,adPushFlag){
    const url = 'users/anonymous';
    const response = await axios.post(
        url,
        {
            'fnlAppVer': appVersion,
            'dvcOsCd': os,
            'pshTkn' : pushToken,
            'adPushFg' : adPushFlag,
        },
        config
    );
    return response.data;
}

export async function getStoresMenusList(strId, prdId) {
    const params = {strId: strId, prdId: prdId};
    const url = "search/stores/" + strId + "/menus/" + strId + "options?" + objectToQueryString(params);
    
    const response = await axios.get(
        url, config
    );
    return response.data;
}

