import React from 'react';
import { Link } from 'react-router-dom';
import LazyLoad from 'react-lazyload';

import * as ACTION from '../common/ActionTypes'

const ListComponent = (props) => {

    return (
        <>

        <div className="listSort">
            <div className="btnWrap leftCol">
                <button type="button" className="btn icon star active">전체</button>
                <button type="button" className="btn">배달</button>
                <button type="button" className="btn">픽업</button>
            </div>
            <div className="btnWrap righttCol">
                <button type="button" className="btn icon filter">필터</button>
            </div>
        </div>
        <div className="listWrap">
            <ul className="listContent">
                {props.data.map((market) =>
                    <li key={market.index} >
                        {market && 
                            <LazyLoad height={100} once >
                                <SingleMarketCmpnt market={market} />
                            </LazyLoad>
                        }
                    </li>
                )}
            </ul>
        </div>
        </>
    )
}

const SingleMarketCmpnt = ({ market }) => {
    console.log(market);

    const thumImgUrl = 'http://images.kisvan.co.kr/smartorder/' + market.imgModNm;
    const distance = (data) => {
        return Math.round(data)
    }

    return (
    <>
        <Link to = {{
                pathname:ACTION.LINK_MARKET_DETAIL+`${market.strId}`,
                state: {
                    img : thumImgUrl,
                    market: market,
                }
            }} >
                
            <div className="listImg">
                {/* <!-- NEW 상품 일떄 --> */}
                <span className="newLabel"><strong>NEW</strong></span>
                <img src={thumImgUrl} alt="썸네일 이미지"/>
            </div>
            <div className="listInfo">
                <div className="infoLabel">
                    {(market.dlvYn === 'Y') ? <span className="label deli">배달</span> : null}
                    {(market.pickYn === 'Y') ? <span className="label pick">픽업</span> : null}
                </div>
                <div className="infoTitle">
                    <p className="title">{market.strNm}</p>
                    <span className="direction">{distance(market.distance)}m</span>
                </div>
                <div className="infoDesc">
                    <ul>
                        <li>
                            <span className="startPoint"><span className="star">{market.starPoint}</span>({market.rvwCnt})</span>
                        </li>
                        <li>
                            <span className="descLabel deliveryTime">{market.dlvTime+' '}</span>
                            <span className="descLabel">배달팁 {market.dlvTip}원</span>
                            <span className="descLabel">최소주문 {market.dlvMinPrice}원</span>
                        </li>
                        <li>
                            <span className="infoLabel">{market.dlvTipDisc === 'Y' ? '배달팁할인 O':'배달팁할인 X'}</span>
                            <span className="infoLabel">{market.extraDisc}</span>
                        </li>
                    </ul>
                </div>
            </div>

        </Link>
    </>
    );
}

export default ListComponent