function PrivateRoute({ children, ...rest }) {
    console.log(children);
    console.log(rest);
    console.log('=================', hasAccessId());
    
    return ( 
        
      <Route {...rest} 
        render={({ location }) =>
        hasAccessId() ? 
            ( children ) : ( <Redirect to={{ pathname: ACTION.LINK_APP_AGREE, state: { from: location }}} /> )
        }
      />
    );
  }

