import React, {useRef, useEffect} from 'react';
import { Link } from 'react-router-dom';

import * as ACTION from '../../common/ActionTypes'
import Joining_Header from '../../components/HeaderCloser'
import useAsync from '../../hook/useAcync';
import * as API from '../../Api'
import {getAccessId} from '../../util/Utils'

export default ({history}) => {

    const idRef = useRef();
    const pwRef = useRef();
    const keepLogin = useRef();

    const mounted = useRef(false)
    useEffect(() => {
        if(!mounted.current) {
          mounted.current = true;
        }
      })



    const doLogin = (e) => {
        e.preventDefault();

        API.signIn(idRef.current.value, pwRef.current.value
            , keepLogin.current.checked ? 1 : 0)
        .then((data) => {
            if(data.code) 
                history.replace(ACTION.LINK_MYPAGE)
            else 
                console.log('로그인 실패')
        })
        .catch((err) => {

        })
    }

    return (
        <div id="wrap">
            <Joining_Header headerTitle='로그인'/>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="sectionRow">
                            <h1 className="logo">
                                슬기로운 배달 생활 슬배생
                            </h1> 
                            <div className="loginForm">
                            <form onSubmit={doLogin} >
                                <ul className="formList">
                                    <li>
                                        <label className="textInput">
                                            <input value="manmanyi@naver.com" ref={idRef} type="text" title="이메일 주소" placeholder="이메일 주소를 입력하세요"/>
                                        </label>
                                    </li>
                                    <li>
                                        <label className="textInput">
                                            <input value="qwer1134!@#" ref={pwRef} type="password" title="비밀번호" placeholder="비밀번호를 입력하세요"/>
                                        </label>  
                                    </li>
                                </ul>
                                <div className="formMore">
                                    <div className="infoCheck">
                                        <label className="checkSelect">
                                            <input ref={keepLogin} type="checkbox"/> <span className="dCheckBox">자동 로그인</span>
                                        </label>
                                    </div>
                                    <div className="infoTxt"> 
                                        <a href="#">아이디 찾기</a> / <a href="#">비밀번호 찾기</a>
                                    </div>
                                </div>
                                <div className="btnWrap">
                                    <button type="submit" className="btn login apply"><strong>로그인</strong></button>
                                </div> 
                            </form>
                            </div>
                        </div>
                        <div className="socialLogin fixedBottom">
                            <ul className="btnWrap">
                                <li>
                                    <a href="#" className="btn icon social btnNaver" onClick={
                                        ()=>{API.logOut()}
                                    }>네이버로 로그인</a>
                                </li>
                                <li>
                                    <a href="#" className="btn icon social btnKakao" onClick={ test}>카카오로 로그인</a>
                                </li>
                                <li>
                                    <a href="#" className="btn icon social btnGoogle">구글로 로그인</a>
                                </li>
                            </ul>
                            <p className="loginInfo">
                                슬배생이 처음이시라면?
                                <Link to={{pathname: ACTION.LINK_SIGNUP_AGREE}} className="goLink">
                                    <strong>회원가입</strong>
                                </Link>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const test  = () => {
    window.appBridge.post('test')
}

const fetchSDLAPI = async (url,method) => {

    const optionPost = {
        method: method,
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json',
            'Authorization' :  getAccessId()
        },
        body: JSON.stringify({
            'mbrId': 'manmanyi@naver.com',
            'mbrPwd': 'qwer1134!@#',
            'isKeepLogin' : 0
        }),
        credentials: 'include'

    }

    const optionGet = {
        method: method,
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json',
            'Authorization' :'Bearer '+ '28e142be4688420fb9a206a8915c2c75'
        },
        credentials: 'include'
    }

    await fetch(url,method === 'POST' ? optionPost : optionGet ,)
    .then(response => response.json())
    .then(response => {
    console.log(JSON.stringify(response));
    console.log('CODE : ' + response.code);
    })
    
}