import React, { useEffect, useState, useCallback } from 'react';
import { Link } from 'react-router-dom';

import WrapPage from "../../../components/WrapPage"
import * as ACTION from '../../../common/ActionTypes'


// 조회성 x
// 기본 사용자 정보: common <- 장바구니 or 메뉴 상세(바로 주문)

export const orderInfo = {
    userYN: "N",
    DP: "픽업",
    marketInfo: {
                marketName: "카페 드롭탑 신도림점",
                marketAddr: "서울시 영등포구 영등포로 143"
                },
    userAddr: "서울시 구로구 구로동 3-25 신도림테크노마트",
    userPhone: "01098765432",
    menus: [
            {menuName: "블랙밀크티",
            amount: 2,
            options: ["펄 기본", "밀크폼 추가"],
            price: 5600
            },
            {menuName: "민트초코라떼",
            amount: 1,
            options: ["휘핑크림"],
            price: 4900
            }
            ],
    delivTip: 2500,
    discounts: [
                {
                    discountName: "추가 할인",
                    discountAmount: 500
                },
                {
                    discountName: "또 할인",
                    discountAmount: 1000
                }
            ],
}

export default ( {history} ) => {

    const [payMethod, setPayMethod] = useState(1);
    const [addrDetail, setAddrDetail] = useState('');
    const [isAccept, setIsAccept] = useState(false);
    const [isValid, setIsValid] = useState(false);

    // const [totalPrice, setTotalPrice] = useState(0);
    // const [totalDiscount, setTotalPrice] = useState(0);
    // const [totalPayment, setTotalPayment] = useState(0);

    let totalPrice = 0;
    let totalDiscount = 0;
    let totalPayment = 0;

    const activePayMethod = useCallback((method) => {
        if(method === payMethod)
            return "active";
        else return "";
    })

    const setTotalPrice = () => {
        orderInfo.menus.map((menu)=>
            totalPrice += menu.price * menu.amount
        );

        totalPrice += orderInfo.delivTip;

        return totalPrice;
    }

    const setTotalDiscount = () => {
        orderInfo.discounts.map((discount)=>
            totalDiscount += discount.discountAmount
        );
        return totalDiscount;
    }

    const setTotalPayment = (totalPrice, totalDiscount) => {
        totalPayment = totalPrice - totalDiscount;

        // console.log(totalPrice+'+'+totalDiscount+'='+totalPayment);

        return totalPayment;
    }

    useEffect(() => {
        let test = addrDetail != '' ? true : false;

        setIsValid(isAccept && test);
        
        // console.log('isAccept: ', isAccept);
        // console.log('주소씀: ', test);
        // console.log('isValid: ', isValid);

        // console.log(orderInfo.DP === "픽업" );

    }, [isAccept, addrDetail])

    const alrtMissing = useCallback(() => {
        console.log('alrtMissing');
        
        if(isAccept)
            console.log('상세주소 안씀');
        else
            console.log('동의 안함');
    })

    const doOrder = useCallback(() => {
        console.log('doOrder');

        console.log('주문가능상태');
    })

    const OrderButton = () => {

        if(!isValid) {
            return (
                <div className="fixedBtn flex3half1" onClick={alrtMissing}>
                    <a className="btn addOrder">(대충 주문 못한다는 뜻)</a>
                </div>);
        } else {
            return (
                
                <div className="fixedBtn flex3half1" /*onClick={doOrder}*/>
                    <Link to = {{pathname:ACTION.LINK_ORDER_SUCCESS}} className="btn addOrder">{totalPayment}원 주문하기</Link>
                </div>
            )
        }
    };

    const onClickBackBtn = (e) => {
        e.preventDefault();
        history.goBack();
      };

    return (
        <WrapPage>
            <div id="wrap">
                <div id="header">
                    <div className="headerTop">
                        <div className="leftArea">
                            <a href="#" className="icon pageBack" onClick={onClickBackBtn}>Back</a>
                        </div>
                        <div className="middleArea">
                            <h1 className="headerTitle">{orderInfo.DP} 주문하기</h1>
                        </div>
                    </div>
                </div>
                <div id="container">
                    <div id="content">
                        { (orderInfo.DP === "픽업") && <PickComp1 /> }

                        {/* <!-- 비회원 주문자 정보 --> */}
                        {(orderInfo.userYN === "N") && 
                        <div className="rowSection">
                            <div className="infoCard">
                                <h2 className="title">주문자 정보</h2>
                                <div className="descBox">
                                    <p className="descInfo">
                                        {orderInfo.userAddr}
                                    </p>

                                    <div className="descInput">
                                        <div className="certifiArea">
                                            <div className="certifiRow">
                                                <label className="textInput" >
                                                    <input type="text" title="상세주소" placeholder="상세주소를 입력하세요" 
                                                            onChange={e=>setAddrDetail(e.target.value)}/>
                                                </label>
                                                <button className="btn defaultBtn">인증번호 받기</button>
                                            </div>
                                            <div className="certifiRow">
                                                <label className="textInput">
                                                    <input type="text" title="전화 번호" value={orderInfo.userPhone} /*onChange={console.log('휴대폰번호 state 처리')} *//>
                                                </label>
                                                <button className="btn completeBtn">완료</button>
                                                <p className="errMsg">인증번호가 일치하지 않습니다.</p>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div> 
                        }

                        {/* <!-- 회원 주문자 정보 --> */}
                        {(orderInfo.userYN === "Y") && 
                        <div className="rowSection">
                            <div className="infoCard">
                                <h2 className="title">주문자 정보</h2>
                                <div className="descBox">
                                    <p className="descInfo">
                                        {orderInfo.userAddr}
                                    </p>
                                    <div className="descInput">
                                        <label className="textInput" >
                                            <input type="text" title="상세주소" placeholder="상세주소를 입력하세요" value="123"/>
                                        </label>
                                        <label className="textInput">
                                            <input type="text" title="전화 번호" value={orderInfo.userPhone} />
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                        }

                        <div className="sectionBlock"></div>
                        <div className="rowSection">
                            <div className="infoCard">
                                <h2 className="title">요청사항</h2>
                                <div className="descBox">
                                    <p className="descInfo">매장에 요청해요~</p>
                                    <div className="descInput">
                                        <label className="textInput" >
                                            <input type="text" title="요청 사항" placeholder="예) 얼음 조금만 넣어주세요." />
                                        </label>
                                    </div>
                                    <div className="descInput">
                                        <label className="checkSelect">
                                            <input type="checkbox" /> <span className="dCheckBox">일회용품 줄이기</span>
                                            <span className="checkInfo">일회용 수저, 포크 안 주셔도 돼요~</span>
                                        </label>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="sectionBlock"></div>
                        <div className="rowSection">
                            <div className="historyCard">
                                <h2 className="title">주문내역</h2>
                                <ul className="historyList">

                                        {orderInfo.menus.map((menu) =>
                                        <li key={menu.menuName}>
                                            <span className="name">
                                            {menu.menuName} x {menu.amount}

                                            <span className="options">{menu.options.map((option) => {return option+' '})}</span>

                                            </span>
                                            <span className="amount">{menu.price*menu.amount}원</span>
                                        </li>
                                        )}

                                    <li>
                                        <span className="name">
                                            배달팁
                                            <a href="#"><span className="icon questionMark">?</span></a>
                                        </span>
                                        <span className="amount">{orderInfo.delivTip}원</span>
                                    </li>
                                </ul>
                                <h2 className="title">할인혜택</h2>
                                <ul className="historyList">
                                        {orderInfo.discounts.map((discount)=>

                                            <li key={discount.discountName}>
                                                <span className="name">{discount.discountName}</span>
                                                <span className="amount">{discount.discountAmount}원</span>
                                            </li>
                                        )}
                                </ul>
                                <h2 className="title">결제금액</h2>
                                <ul className="historyList">
                                    <li>
                                        <span className="name">
                                            주문금액
                                        </span>
                                        <span className="amount">
                                            {setTotalPrice()}원
                                        </span>
                                    </li>
                                    <li>
                                        <span className="name">
                                            할인금액
                                        </span>
                                        <span className="amount">
                                            -{setTotalDiscount()}원
                                        </span>
                                    </li>
                                </ul>
                                <div className="historyTotal">
                                    <span className="name">총 결제금액</span>
                                    <span className="amount">{setTotalPayment(totalPrice, totalDiscount)}원</span>
                                </div>
                            </div>
                        </div>
                        <div className="sectionBlock"></div>
                        <div className="rowSection">
                            <div className="infoCard">
                                <h2 className="title">결제방법</h2>
                                <div className="payWayList">
                                    <ul className="payWay">
                                        <li className={activePayMethod(1)} onClick={()=>setPayMethod(1)}>
                                            <a>신용카드</a>
                                        </li>
                                        <li className={activePayMethod(2)} onClick={()=>setPayMethod(2)}>
                                            <a>휴대폰 결제</a>
                                        </li>
                                        <li className={activePayMethod(3)} onClick={()=>setPayMethod(3)}>
                                            <a>계좌이체</a>
                                        </li>
                                        <li className={activePayMethod(4)} onClick={()=>setPayMethod(4)}>
                                            <a>가상 계좌</a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        { (orderInfo.DP === "픽업") && <PickComp2 /> }
                        <div className="rowSection">
                            <div className="agreeBox">
                                <ul className="agreelist">
                                    <li>
                                        <span>
                                            이용약관 및 동의
                                        </span>
                                        <a href="#" className="viewList">내용보기</a>
                                    </li>
                                    <li>
                                        <span>
                                            개인정보 수집 및 이용동의
                                        </span>
                                        <a href="#" className="viewList">내용보기</a>
                                    </li>
                                    <li>
                                        <span>
                                            개인정보 제3자 제공동의
                                        </span>
                                        <a href="#" className="viewList">내용보기</a>
                                    </li>
                                    <li>
                                        <span>
                                            위치기반서비스약관
                                        </span>
                                        <a href="#" className="viewList">내용보기</a>
                                    </li>
                                    <li>
                                        <span>
                                            전자금융거래 이용약관
                                        </span>
                                        <a href="#" className="viewList">내용보기</a>
                                    </li>
                                </ul>
                                <label className="checkSelect">
                                    <input type="checkbox" onChange={e=>setIsAccept(e.target.checked)}/> <span className="dCheckBox">위의 내용을 확인하였으며 결제에 동의합니다.</span>
                                </label>
                            </div>
                        </div>
                        <OrderButton/>
                    </div>
                </div>
            </div>
        </WrapPage>
    );
};

// export default function OrderSection() {
//     return (
//         <>
//             <MakeOrder/>
//         </>
//     );
// };

export const PickComp1 = () => {

    return ( <>
        <div className="sectionBlock"></div>
        <div className="rowSection">
            <div className="infoCard">
                <h2 className="title">매장 정보</h2>
                <div className="descBox">
                    <p className="descInfo">
                        {orderInfo.marketInfo.marketName}
                    </p>
                    <p className="descInfoSmall">
                        {orderInfo.marketInfo.marketAddr}
                        <button type="button" className="btn borderBtn btnSmall">주소복사</button>
                    </p>
                </div>
            </div>
        </div>
    </>);
}

export const PickComp2 = (render) => {
    return (
        <>
            <div className="sectionBlock"></div>
            <div className="rowSection">
                <p className="pickInfoMsg">
                    고객님께서는 픽업 주문을 하고 계십니다.<br />
                    매장으로 직접 방문해주세요~
                </p>
            </div>
        </>
        );
}