import React, { useState } from 'react';

export default ({history}) => {

    const handleOnClick = (e) =>{
        e.preventDefault();
        history.goBack();
    }

    const fnToggleBtn = (e) =>{
        var items = document.querySelectorAll('.toggleList ul li')        
        if(e.currentTarget.parentNode.className  === "active"){
            e.currentTarget.parentNode.className = ""
        }else{
            for(var i =0; i < items.length; i++){
                items[i].className = "";
            }
            e.currentTarget.parentNode.className = "active"
        }
    }
    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a onClick={handleOnClick} className="icon pageBack">Back</a>
                    </div>
                    <div className="middleArea">
                        <h1 className="headerTitle">FAQ</h1>
                    </div>
                    {/* <!-- <div className="rightArea">
                        <a href="#" className="icon setting">
                            설정
                        </a>
                    </div> --> */}
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="">
                        <div className="tabBtnArea">
                            <ul className="btnList">
                                {/* <!-- active 클래스 추가 --> */}
                                <li className="active">
                                    <a href="#">전체</a>
                                </li>
                                <li>
                                    <a href="#">회원정보</a>
                                </li>
                                <li>
                                    <a href="#">주문접수</a>
                                </li>
                                <li>
                                    <a href="#">주문확인</a>
                                </li>
                                <li>
                                    <a href="#">결제</a>
                                </li>
                                <li>
                                    <a href="#">취소/환불</a>
                                </li>
                            </ul>
                        </div>
                        <div className="toggleList">
                            <ul>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>아이디와 비밀번호가 기억나지 않아요.</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                            주문 상태가 완료된 이후에는 주문한 메뉴(혹은 수량)  변경을 하거나 직접 취소를 하실 수 없습니다.<br />
                                            주문상태가 수락전이라면 이라면 고객센터로 연락하여 주문 취소 후 재구매가 가능합니다.
                                            만약 주문상태가 수락 후 라면 매장 정보에 가맹점의 전화번호로 직접 전화를 주시어 (변경/취소)가능여부를 확인하여 주시기 바랍니다.
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>회원 탈퇴는 어떻게 하나요?</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                            주문 상태가 완료된 이후에는 주문한 메뉴(혹은 수량)  변경을 하거나 직접 취소를 하실 수 없습니다.
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>주문한 메뉴를 변경하거나 취소하고 싶습니다.</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                            주문 상태가 완료된 이후에는 주문한 메뉴(혹은 수량)  변경을 하거나 직접 취소를 하실 수 없습니다.<br />
                                            주문상태가 수락전이라면 이라면 고객센터로 연락하여 주문 취소 후 재구매가 가능합니다.
                                            만약 주문상태가 수락 후 라면 매장 정보에 가맹점의 전화번호로 직접 전화를 주시어 (변경/취소)가능여부를 확인하여 주시기 바랍니다.
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }}  className="toggleTitle fnToggleList">
                                        <p>전화로도 주문할 수 있나요?</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                            주문 상태가 완료된 이후에는 주문한 메뉴(혹은 수량)  변경을 하거나 직접 취소를 하실 수 없습니다.<br />
                                            주문상태가 수락전이라면 이라면 고객센터로 연락하여 주문 취소 후 재구매가 가능합니다.
                                            만약 주문상태가 수락 후 라면 매장 정보에 가맹점의 전화번호로 직접 전화를 주시어 (변경/취소)가능여부를 확인하여 주시기 바랍니다.
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }}  className="toggleTitle fnToggleList">
                                        <p>장바구니 이용 방법이 궁금합니다.</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                            주문 상태가 완료된 이후에는 주문한 메뉴(혹은 수량)  변경을 하거나 직접 취소를 하실 수 없습니다.<br />
                                            주문상태가 수락전이라면 이라면 고객센터로 연락하여 주문 취소 후 재구매가 가능합니다.
                                            만약 주문상태가 수락 후 라면 매장 정보에 가맹점의 전화번호로 직접 전화를 주시어 (변경/취소)가능여부를 확인하여 주시기 바랍니다.
                                        </p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            {/* <!-- <div id="nav">
                <div>
                    <ul className="navList">
                        <li className="home active"><a href="#">홈</a></li>
                        <li className="map"><a href="#">주변지도</a></li>
                        <li className="myPage"><a href="#">my슬배생</a></li>
                        <li className="myOrder"><a href="#">주문내역</a></li>
                        <li className="myLike"><a href="#">마이찜</a></li>
                    </ul>
                </div>
            </div> --> */}
        </div>
    )
};