import React, { useRef, useState, useEffect, useCallback, useMemo } from 'react';
import LazyLoad from 'react-lazyload';

import useAsync from '../../hook/useAcync' 
import * as API from '../../Api'
import ListComponent from '../../components/listComponent'
import {getRecentSearch,setRecentSearch,removeRecentSearch} from '../../util/Utils'

//export default ({history}) => {

const Search = ({history, location}) => {

    const inputSearchRef = useRef();
    
    const latitude = location.state.latitude;
    const longitude = location.state.longitude;

    const [recentSearch, setRecenetSearch] = useState([]);
    const [searchResult, setSearchResult] = useState(null);

    const btnSearchClick = useCallback(()=>{

        if(inputSearchRef.current.value !== "") {
            setRecentSearch(inputSearchRef.current.value);
    
            API.getSearch(latitude, longitude, inputSearchRef.current.value)
            .then((data)=>{
                console.log('SUCCESS: ', data)
                setSearchResult(data)})
            .catch((error) => {
                console.log('FAIL: ', error)
                setSearchResult([]);
            })
        }
    })

    const btnBackClick = () =>{
        history.goBack();
    };

    const btnClearClick = () => {
        inputSearchRef.current.value=''
    };

    const btnRemoveSearch =  (e) => {
        const data = e.target.dataset.value
        removeRecentSearch(data);
        setRecenetSearch(getRecentSearch());
    }

    useEffect(() => {
        const searchs = getRecentSearch();
        setRecenetSearch(searchs);
    }, []);

    useEffect( () => {
    },[recentSearch, searchResult])

    return (

        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a onClick={btnBackClick} className="icon pageBack">Back</a>
                    </div>            
                    <div className="middleArea">
                        <div className="searchHeader">
                            <span className="textInput withDel">
                                <input type="text" title="검색" placeholder="음식명이나 가게명으로 검색하세요." ref = {inputSearchRef}/>
                                <button className="delInput" type="button" onClick={btnClearClick}>삭제</button>
                            </span>
                            <button className="icon searchBtn" type="button" onClick={btnSearchClick}/>
                        </div>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    {searchResult !== null && searchResult.length > 0  ? 'asdasdsad' : <Content1 searchResult = {searchResult} recentSearch = {recentSearch} btnRemoveSearch={btnRemoveSearch}/>}
                </div>
            </div>
        </div>
    )

    
}

const Content1 = (props) => {
    
    if(props.searchResult !== null && props.searchResult.data.length > 0){
        return (
            
            <ListComponent data={props.searchResult.data}/>

            // props.searchResult.data.map((list) => {
            //     console.log(list)
            // })
        )
    }

    else if(props.searchResult !== null && props.searchResult.data.length === 0){
        return (
            <div>
                <div className="staticTitleView">
                    <div>
                        <p className="infoSearchMsg">
                            휴게소 검색을 원하신다면<br />
                            고속도로명 또는 휴게소명으로 검색해 보세요
                        </p>
                    </div>
                </div>
                <div className="sectionBlock"></div>
                <div className="emptyWrap typeSearch">
                    <div className="empty">
                        <p className="emptyMsg_1">검색 결과가 없습니다.</p>
                        <p className="emptyMsg_2">
                            고객님이 설정한 위치를 기준으로<br />
                            배달/픽업이 가능한 가게만 검색됩니다.
                        </p>
                    </div>
                </div>
            </div>
        )
    }

    return (
        <>
            <div className="staticTitleView">
                <div>
                    <p className="infoSearchMsg">
                        휴게소 검색을 원하신다면<br />
                        고속도로명 또는 휴게소명으로 검색해 보세요
                    </p>
                </div>
            </div>
            <div className="sectionBlock">
                <div className="searchRecently">
                    <h2 className="title">최근 검색어
                        <button type="button" className="delAll">전체삭제</button>
                    </h2>
                    {
                        props.recentSearch.length > 0 && 
                        <ul className="recentlyList">
                            {props.recentSearch.map((data,index) => {
                                return (
                                    <li key={index}>
                                    {data}
                                    <button data-value={data} type="button" className="del" onClick={props.btnRemoveSearch}>삭제</button>
                                    </li>
                                )
                                
                            })}
                        </ul>
                    }
                </div>
            </div>
            
        </>
        
    )
}
const ResultComponent = (props) => {
    return (
        function () {
            if(props.error === null  && props.data.length <= 0) {
                return (
                    <div>
                        <div className="staticTitleView">
                            <div>
                                <p className="infoSearchMsg">
                                    휴게소 검색을 원하신다면<br />
                                    고속도로명 또는 휴게소명으로 검색해 보세요
                                </p>
                            </div>
                        </div>
                        <div className="sectionBlock"></div>
                        <div className="emptyWrap typeSearch">
                            <div className="empty">
                                <p className="emptyMsg_1">검색 결과가 없습니다.</p>
                                <p className="emptyMsg_2">
                                    고객님이 설정한 위치를 기준으로<br />
                                    배달/픽업이 가능한 가게만 검색됩니다.
                                </p>
                            </div>
                        </div>
                    </div>
                )
            }
            else if(props.data.length > 0) {
                return (
                    props.data.map((list) => {
                        console.log(list)
                    })
                )
            }
            else {
                return (
                    <div>
                        <div className="staticTitleView">
                            <div>
                                <p className="infoSearchMsg">
                                    휴게소 검색을 원하신다면<br />
                                    고속도로명 또는 휴게소명으로 검색해 보세요
                                </p>
                            </div>
                        </div>
                        <div className="sectionBlock"></div>
                        <div className="searchRecently">
                            <h2 className="title">최근 검색어
                                <button type="button" className="delAll">전체삭제</button>
                            </h2>
                            <ul className="recentlyList">
                                {props.search.map((data,index) => {
                                    return (
                                        <li key={index}>
                                        <a href="#">김밥</a>
                                        <button type="button" className="del">삭제</button>
                                        </li>
                                    )
                                    
                                })}
                                <li>
                                    <a href="#">김밥</a>
                                    <button type="button" className="del">삭제</button>
                                </li>
                                <li>
                                    <a href="#">커피</a>
                                    <button type="button" className="del">삭제</button>
                                </li>
                                <li>
                                    <a href="#">치킨</a>
                                    <button type="button" className="del">삭제</button>
                                </li>
                            </ul>
                        </div>
                    </div>
                )
            }
        }()
    )
    
}

export default Search