import React, { useEffect, useState, useCallback, useRef } from 'react';
import { Link } from 'react-router-dom';

import WrapPage from "../../../components/WrapPage"
import * as ACTION from '../../../common/ActionTypes'

// 조회성 x
// 기본 사용자 정보: common <- 장바구니 or 메뉴 상세(바로 주문)

export const orderInfo = {
    userYN: "N",
    DP: "픽업",
    marketInfo: {
                marketName: "카페 드롭탑 신도림점",
                marketAddr: "서울시 영등포구 영등포로 143"
                },
    userAddr: "서울시 구로구 구로동 3-25 신도림테크노마트",
    userPhone: "01098765432",
    menus: [
            {menuName: "블랙밀크티",
            amount: 2,
            options: ["펄 기본", "밀크폼 추가"],
            price: 5600
            },
            {menuName: "민트초코라떼",
            amount: 1,
            options: ["휘핑크림"],
            price: 4900
            }
            ],
    delivTip: 2500,
    discounts: [
                {
                    discountName: "추가 할인",
                    discountAmount: 500
                },
                {
                    discountName: "또 할인",
                    discountAmount: 1000
                }
            ],
}

export default ( {history} ) => {

    const [seconds, setSeconds] = useState(5);
    const timeouts = useRef();

    useEffect(() => {
        return () => {
            clearTimeout(timeouts.current)
            timeouts.current = [];
        }
    }, [])

    useEffect(() => {
        timeouts.current = setTimeout(() => {
            setSeconds(seconds -1);
        }, 1 * 1000);
    })

    useEffect(() => {
        if(seconds === 0) {
            history.push(ACTION.LINK_ORDER_HISTORY);
            // history.replace(ACTION.LINK_ORDER_HISTORY);
        }
    }, [seconds])

    return (
        <WrapPage>
            <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    
                    <div className="leftArea">
                        <Link to = {{pathname: ACTION.LINK_HOME}} className="icon pageClose">CLOSE</Link>
                    </div>
                    
                    <div className="middleArea">
                        <h1 className="headerTitle">배달 주문완료</h1>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="msgBox">
                            <p className="orderComplate">
                                <strong>카페 드롭탑 신도림점</strong><br />
                                매장에서 주문을 <strong>접수중</strong>입니다.
                            </p>                        
                        </div>
                        <div className="countMsg">
                            <p>
                                주문상태에 대한 확인을 위해<br />
                                <span>{seconds}</span>초 후 주문내역 페이지로 자동 이동합니다.
                            </p>
                        </div>                    
                        <div className="orderInfoList">
                            <ul className="infoLIst">
                                <li>
                                    <strong className="leftCell">픽업주소</strong>
                                    <span className="rightCell">
                                        <span>서울시 구로구 새말로 97</span>
                                        <span className="moreInfo">2301호</span>
                                    </span>
                                </li>
                                <li>
                                    <strong className="leftCell">주문내역</strong>
                                    <span className="rightCell">
                                        <span>오곡라떼 x 2</span>
                                        <span className="options">펄 기본, 휘핑크림</span>
                                    </span>
                                </li>
                                <li>
                                    <strong className="leftCell">결제금액</strong>
                                    <span className="rightCell">
                                        <strong className="amount">9,600원</strong>
                                        <span className="moreInfo">바로결제 / 신용카드</span>
                                    </span>
                                </li>
                            </ul>
                        </div>
                        <p className="bottomMsg">
                            주문 후, 조리가 시작되면 취소할 수 없습니다.<br />
                            주문 접수가 승인되면  방문 예정 시간을 안내해드립니다.
                        </p>
                    </div>
                    <div className="fixedBtn">
                        <a href="#" className="btn addCart">홈으로</a>
                        <Link to = {{pathname:ACTION.LINK_ORDER_HISTORY}} className="btn addOrder">주문내역 확인</Link>
                    </div>
                </div>
            </div>
        </div>
        </WrapPage>
    );
};