import React, { useEffect, useState, useCallback, useRef } from 'react';
import { Link } from 'react-router-dom';

import * as ACTION from '../../../common/ActionTypes'

// 조회성 x
// 기본 사용자 정보: common <- 장바구니 or 메뉴 상세(바로 주문)

const orderHistoryList = [
    {
        index: 0,
        DP: 0, 
        status: 0,    // 0: 배달 중
        time: '오후 5:20 도착 예정'  ,
        name: '카페 이디야 신도림점',
        menu: ['오곡라떼', '어쩌고', '다른거'],
        price: 8800
      },
      {
        index: 1,
        DP: 0, 
        status: 1,    //1: 배달 완료
        time: '2020.05.18 오전 08:10'  ,
        name: '카페 탐탐 신도림점',
        menu: ['아메리카노', '딴거', '어쩌고', '다른거'],
        price: 12000
      },
      {
        index: 2,
        DP: 1, 
        status: 2,    // 2: 주문 완료
        time: '오후 10:10'  ,
        name: '맛없는 돈까스 신도림점',
        menu: ['치즈돈까스'],
        price: 9000
      },
      {
        index: 3,
        DP: 1, 
        status: 3,    // 3: 픽업 완료
        time: '2020.05.17 오후 12:10'  ,
        name: '맛잇는 돈까스 구디',
        menu: ['왕돈까스', '딴거'],
        price: 13000
      },
      

  ];

  
  export default ( {history} ) => {
      
    const [historyData, setHistoryData] = useState([]);
    const [filter, setFilter] = useState(2);

    useEffect(() => {
        setHistoryData([...orderHistoryList]);
    }, [])

    const doFilter = (DP) => {
        const style = {
            display: 'none'
        }
        if(DP === filter) return '{}';
        else return style;
    }

    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="middleArea">
                        <h1 className="headerTitle">주문내역</h1>
                    </div>
                    <div className="rightArea">
                        <a href="#" className="icon reflash">
                            새로고침
                        </a>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="listSort pageSort">
                            <div className="btnWrap leftCol">
                                {/* <!-- active 클래스 추가 & 삭제 --> */}
                                <button type="button" className={filter === 2 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(2)}>전체</button>
                                <button type="button" className={filter === 0 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(0)}>배달</button>
                                <button type="button" className={filter === 1 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(1)}>픽업</button>
                            </div>                      
                        </div>
                        <div className="sectionBlock" ></div>
                        { historyData.map((data) => 
                        <>
                            <div  key={data.index}className="orderInfoList typeResult" style={{display: (data.DP === filter || filter === 2) ? 'block': 'none'}}>
                                { <SingleHistoryComponent data={data} /> }
                            </div>
                            <div className="sectionBlock" style={{display: (data.DP === filter|| filter === 2)? 'block': 'none'}}></div>
                        </>
                        )}

                    </div>
                </div>
            </div>
            <div id="nav">
                <div>
                <ul className="navList">
                    <li className="home"><Link to={{pathname: ACTION.LINK_HOME}}>홈</Link></li>
                    <li className="map"><Link to={{pathname: ACTION.LINK_AROUND_MAP}}>주변지도</Link></li>
                    <li className="myPage"><Link to={{pathname: ACTION.LINK_MYPAGE}}>my슬배생</Link></li>
                    <li className="myOrder active"><Link to={{pathname: ACTION.LINK_ORDER_HISTORY}}>주문내역</Link></li>
                    <li className="myLike"><Link to={{pathname: ACTION.LINK_MY_JJIM}}>마이찜</Link></li>
                </ul>
                </div>
            </div>
        </div>
    );
};

const SingleHistoryComponent = ( {data} ) => {

    const timeSwitch = (status) => {
        switch(status) {
            case 0:
                return (
                <>
                    배달중
                    <a href="#" className="moreDate orderIng">{data.time}</a>
                </>
                );
            case 1:
                return (
                <>
                    배달 완료
                    <a href="#" className="moreDate">{data.time}</a>

                </>
                );
            case 2:
                return (
                <>
                    주문 완료
                    <a href="#" className="moreDate orderIng">{data.time} 방문해주세요</a>
                </>
                );
            case 3:
                return (
                <>
                    픽업 완료
                    <a href="#" className="moreDate">{data.time}</a>
                </>
                );
                default:

                break

        }
    }

    const btnSwitch = (status) => {
        switch(status) {
            case 0:
            case 2:
                return (
                <> 
                    <ul className="btnInner">
                        <li>
                            <Link to={{pathname: ACTION.LINK_ORDER_DETAIL
                                /* , state:  {
                                    data: history index 태우기
                                }
                                */
                            }} className="btn icon detail">
                                주문상세
                            </Link>
                        </li>
                    </ul>
                </>
                );
            case 1:
            case 3:
                return (
                <>
                    <ul className="btnInner">
                        <li><Link to={{pathname: ACTION.LINK_MAKE_REVIEW}} className="btn icon review">리뷰쓰기</Link></li>
                        {/* <!-- 추후 disable 클래스 추가 삭제 처리 --> */}
                        <li>
                            <Link to={{pathname: ACTION.LINK_ORDER_DETAIL
                            /* , state:  {
                                data: history index 태우기
                            }
                            */
                            }} className="btn icon detail">주문상세</Link>
                        </li>
                    </ul>
                </>
                );
                default:

                break
        }
    }

    return (
        <>
            <h2 className="listTitle">
                <div className="statusLabel">
                {data.DP === 0 ?
                    <span className="label deli">배달</span>
                    : <span className="label pick">픽업</span>
                }
                </div>
                { timeSwitch(data.status) }
            </h2>
            <div className="infoBox">
                <div className="infoInner">
                    <p className="title"><strong>{data.name}</strong><a href="#" className="btnMenu">메뉴</a></p>
                    <p className="option">{data.menu[0]} {data.menu.length > 1 ? (' 외 '+(data.menu.length-1)+'개') : ''} {data.price}원</p>
                </div>
                <div className="btnWrap">
                    { btnSwitch(data.status) }
                </div>
            </div>
        </>
    )
}