import React from 'react'

export default ({ history, location }) => {
    const onClickBackBtn = (e) => {
        e.preventDefault();
        history.goBack();
      };
    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a href="#"  onClick={onClickBackBtn} className="icon pageClose">CLOSE</a>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="">
                        <div className="bannerWrap">
                            <div className="bannerDetail">
                                <img src="/common/images/banner/bnr_tump_detail.jpg" alt="이벤트" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
