import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import * as ACTION from '../../../common/ActionTypes'
import useAsync from '../../../hook/useAcync'
import * as API from '../../../Api'

import CircularProgress from '@material-ui/core/CircularProgress';

import ListTabMenu from './tab'
import ListComponent from '../../../components/listComponent'


export default ({ history, location }) => {

    const onClickBackBtn = (e) => {
        e.preventDefault();
        history.goBack();
    };

    const [state, refetch] = useAsync(() => API.getStores(location.state.latitude, location.state.longitude, location.state.bizCtgDtl), []);
    const { loading, data, error } = state;

    console.log('state', state)

    if (loading) return (
        <div className="fullPage">
            <div className='loading'><CircularProgress /></div>
        </div>
    );
    if (error) return (
        <div className="fullPage">
            <div class="error">에러가 발생했습니다</div>
        </div>
    );
    if (!data) return null;

    return (
        <>
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a /*href="#" */ className="icon pageBack" onClick={onClickBackBtn}>Back</a>
                    </div>
                    <div className="middleArea">
                        <Link to={{ pathname: ACTION.LINK_ADDRESS_SETTING }} className="addressBox">
                            구로구 새말로 97
                      </Link>
                    </div>
                    <div className="rightArea">
                        <Link to={ACTION.LINK_ORDER} className="icon orderCart">
                            장바구니
                          <span className="cartNum">1</span>
                        </Link>
                    </div>
                </div>
            </div>

            <div id="wrap">
                <div id="container">
                    <div id="content">
                        <div>
                            <ListTabMenu></ListTabMenu>
                            <ListComponent data={data.data}/>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

