import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import * as ACTION from '../../../common/ActionTypes'

export default ({history}) => {

    // dummy data
    const NoticeData = [
        {
        index: 0,
        title: "first",
        date: "2020.07.07",
        contents: <>
                안녕하세요. 슬배생입니다.<br /><br />
                스마트오더 2.0의 새 이름 슬배생!<br />
                슬배생 앱이 출시되었습니다!<br /><br />    
                보다 나은 서비스를 위해 노력하겠습니다.<br />
                감사합니다.
                </>
        },
        {
        index: 1,
        title: "second",
        date: "2020.07.08",
        contents: <>
                안녕하세요. 슬배생입니다.<br /><br />
                스마트오더 2.0의 새 이름 슬배생!<br />
                슬배생 앱이 출시되었습니다!<br /><br />    
                보다 나은 서비스를 위해 노력하겠습니다.<br />
                감사합니다.
                </>
        }
    ];
    // states
    const [NoticeList, setNoticeList] = useState([]);

    useEffect(() => {
        setNoticeList([...NoticeData]);
        console.log(NoticeList);
    }, []);

    const handleOnClick = (e) =>{
        e.preventDefault();
        history.goBack();
    }
    
    return (
    <div id="wrap">
        <div id="header">
            <div className="headerTop">
                <div className="leftArea">
                    <a onClick={handleOnClick} className="icon pageBack">Back</a>
                </div>
                <div className="middleArea">
                    <h1 className="headerTitle">공지사항</h1>
                </div>
            </div>
        </div>
        <div id="container">
            <div id="content">
                <div className="fullHeight">
                    <div className="arrowList typeNotice">
                        <ul>
                        { NoticeData.map((notice) => 
                            <li key={notice.index}>
                                { notice && <SingleNoticeComponent notice={notice} />}
                            </li>
                        )}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
    );
};



const SingleNoticeComponent = ({notice}) => {

    useEffect(() => {
        console.log(`/mysdl/notice/${notice.index}`)
    })

    /* export const LINK_NOTICE_PAGE = '/mysdl/notice/:index'; */
    /* ACTION.LINK_NOTICE */ 
    return (
        <Link to={{pathname: ACTION.LINK_NOTICE_DETAIL + notice.index
            , data: notice //history index 태우기
        }} className="listLine">
            <span className="date">
                {notice.date}
                <span className="iconNew">NEW</span>{/* 신규 공지 */}
            </span>
            <p>{notice.title}</p>
        </Link>
    )
}