import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';

import Joining_Header from '../../../components/HeaderCloser'

export default () => {
    return (
        <div id="wrap">
            <Joining_Header headerTitle='회원가입'/>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="sectionRow">
                            <div className="loginForm">
                                <div className="btnWrap">
                                    <strong className="btn login disable">휴대폰 본인 인증완료</strong>
                                </div>
                                <ul className="registerList">
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_id">아이디</label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="email" id="register_id" placeholder="이메일 주소를 입력하세요"/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_pw">비밀번호</label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="password" id="register_pw" placeholder="영문+숫자+특수문자 조합 8자리 이상"/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_ck">비밀번호 확인</label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="password" id="register_ck" placeholder="비밀번호를 다시 입력해주세요"/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_nic">닉네임</label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="text" id="register_nic" placeholder="2자~10이내로 닉네임을 입력해주세요"/>
                                            </span>
                                        </span>
                                    </li>
                                </ul>
                            </div>
                            <div className="fixedBottom">
                                <div className="btnWrap fullWidth">
                                    <Link to="/Joining_4">
                                        <button type="submit" className="btn login disable"><strong>완료</strong></button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
