import React, { useState, useEffect } from 'react';

import {setAccessId} from '../../util/Utils'
import * as API from '../../Api'
import {closeApp} from '../../appBridge'
import {getQueryStringParams} from '../../util/Utils'




export default ({history,location}) => {

    const [isLoading,setLoading] = useState(false);
    const [allAgree, setAllAgree] = useState(false);
    const [agree1, setAgree1] = useState(false);
    const [agree2, setAgree2] = useState(false);
    
    let { from } = location.state || { from: { pathname: "/" } };

    const btnConfirmClick = (e) => {
        
        // os=AN&version=1&token=dsfjkldjflkdj2123123213
        const param = getQueryStringParams(location.state.from.search)
        console.log('os: ' , param.os);
        console.log('os: ' , param.version);
        console.log('os: ' , param.token);

        console.log('agree1', agree1);

        if(agree1 === false){
            alert('위치기반 서비스 약관 동의는 필수입니다.');
            return;
        }

            setLoading(true);

            API.anonymous(param.version,param.os,param.token,0)
            .then((data) => {
                console.log('resonseData',data.data.accessId);
                setAccessId(data.data.accessId)
                history.replace(from);
                // setTimeout(()=>{history.replace(from);},1500)
                
                
                setLoading(false);
            })
            .catch((err) => {
                setLoading(false);
            })

    }

    const btnFinishClick = () => {
        
    }

    const allAgreeChangListener = (e) => {
        let checked = e.target.checked;
        console.log('allAgreeChangListener',checked);
        setAllAgree(checked);
        if(checked){
            setAgree1(true);
            setAgree2(true);
        }else{
            setAgree1(false);
            setAgree2(false);
        }
    }

    const agree1ChangListener = (e) => {
        let checked = e.target.checked;
        console.log('agree1ChangListener',checked);
        if(!checked){
            setAllAgree(false);
        }else{
            if(agree2===true){
                setAllAgree(true)
            }
        }

        setAgree1(checked);
    }

    const agree2ChangListener = (e) => {
        let checked = e.target.checked;
        console.log('agree2ChangListener',checked);
        if(!checked){
            setAllAgree(false);
        }else{
            if(agree1===true){
                setAllAgree(true)
            }
        }
        setAgree2(checked);
    }

    
    useEffect(() => {
        
    },[agree1,agree2])


    return(
        <div id="wrap">
            <div id="header">
                <div class="headerTop">
                    <div class="leftArea">
                        <button onClick={btnFinishClick} class="icon pageClose">CLOSE</button>
                    </div>            
                    <div class="middleArea">
                        <h1 class="headerTitle">이용약관 동의</h1>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div class="fullHeight">
                        <div class="sectionRow">
                            <div class="loginForm">
                                <div class="agreeWelcome normal">
                                    <p class="welcomeMsg">
                                        <strong>아래의 약관에 동의하신 후<br />
                                        슬배생 서비스를 이용해주세요.</strong>
                                    </p>
                                    <div class="welcomeCheck">
                                        <label class="checkSelect">
                                            <input type="checkbox" checked={allAgree} onChange={allAgreeChangListener}/> <strong class="dCheckBox">전체동의</strong>
                                        </label>
                                    </div>
                                </div>
                                <ul class="agreeList default">
                                    <li>
                                        <span>
                                            <label class="checkSelect">
                                                <input type="checkbox" checked={agree1} onChange={agree1ChangListener}/> <span class="dCheckBox">위치 기반 서비스 약관 동의 <span class="importTxt">(필수)</span></span>
                                                <button class="checkDetail">자세히 보기</button>
                                            </label>
                                        </span>
                                    </li>
                                    <li>
                                        <span>
                                            <label class="checkSelect">
                                                <input type="checkbox" checked={agree2} onChange={agree2ChangListener}/> <span class="dCheckBox">마케팅 정보 앱 푸시 알림 수신 동의 (선택)</span>
                                                <span class="checkDetail">이벤트 및 혜택 정보를 받아 보실 수 있습니다.</span>
                                            </label>
                                        </span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="fixedBottom">
                        <div class="btnWrap fullWidth">
                            <button class="btn login disable" onClick={btnConfirmClick}><strong>확인</strong></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    )
}

