import React, { useState, useEffect } from 'react';

import useAsync from '../../../hook/useAcync' 
import * as API from '../../../Api'
import * as ACTION from '../../../common/ActionTypes'

const MenuComponent = (props) => {

    // strId -------------- 매장ID
    // PrdId -------------- 상품ID

    //     MainDish: '',         // 메인메뉴인가(틀리면=0,맞으면=1)
    //     StoreName: '',        // 매장명
    //     MenuName:'',          // 메뉴이름
    //     MenuContents:'',      // 메뉴 설명
    //     Price:'',             // 가격
    //     Option1:'',           // 옵션1
    //     Option1Option:0,      // 옵션1의 선택방법(radio=0,check=1)
    //     Option1Ditail1_1:'',  // 옵션1의 선택지1
    //     Option1Ditail1_2:'',  // 옵션1의 선택지1
    //     Option1Ditail2_1:'',  // 옵션1의 선택지2
    //     Option1Ditail2_2:'',  // 옵션1의 선택지2
    //     Option2:'',           // 옵션2
    //     Option2Option:0,      // 옵션2의 선택방법(radio=0,check=1)
    //     Option2Ditail1_1:'',  // 옵션2의 선택지1
    //     Option2Ditail1_2:'',  // 옵션2의 선택지1
    //     Option2Ditail2_1:'',  // 옵션2의 선택지2
    //     Option2Ditail2_2:'',  // 옵션2의 선택지2
    //     MinAmount:0           // 최소주문금액

    // states
    const [Basket, setBasket] = useState(0);
    const [StoreName, setStoreName] = useState("");
    const [MainDish, setMainDish] = useState("");
    const [ImgSrc, setImgSrc] = useState("");
    const [MenuName, setMenuName] = useState("");
    const [MenuContents, setMenuContents] = useState("");
    const [Price, setPrice] = useState(0);
    const [MinAmount, setMinAmount] = useState(0);
    
    const [TotalPrice, setTotalPrice] = useState(0);
    const [TotalCount, setTotalCount] = useState(1);
    const [UserChoiceCheck, setUserChoiceCheck] = useState([]);

    
    const [CreateOption, setCreateOption] = useState([]);


    // API getStoresMenusList 사용해야함
    const [state, refetch] = useAsync(() => API.getStoresMenusList(props.strId, props.prdId), []);
    const { loading, data, error } = state;




    useEffect(() => {
        let sumCheck = 0;

        CreateOption.map((detailOption) => (
            detailOption.Detail.map((option) => (
                UserChoiceCheck.map((id) => (
                    function() {
                        if(option.id === id) {
                            sumCheck += option.Detail3
                        }
                    } ()
                ))
            ))
        ))

        setTotalPrice((sumCheck + Price) * TotalCount)
    }, [TotalCount, UserChoiceCheck]);

    useEffect(() => {
        setBasket(MenuInfoData[0].Basket);
        setStoreName(MenuInfoData[0].StoreName);
        setMainDish(MenuInfoData[0].MainDish);
        setImgSrc(MenuInfoData[0].ImgSrc);
        setMenuName(MenuInfoData[0].MenuName);
        setMenuContents(MenuInfoData[0].MenuContents);
        setPrice(MenuInfoData[0].Price);
        setMinAmount(MenuInfoData[0].MinAmount);
        setTotalPrice(MenuInfoData[0].Price);
       
        for(let ind=0; ind < MenuInfoData[0].Option.length; ind++) {
            let CreateOptionDetail = []

            CreateOptionDetail.OptionSub=MenuInfoData[0].Option[ind].OptionSub
            CreateOptionDetail.OptionOfOption=MenuInfoData[0].Option[ind].OptionOfOption
            CreateOptionDetail.defaultOptionId=MenuInfoData[0].Option[ind].Detail[0].id
            CreateOptionDetail.radioIndex=ind
            CreateOptionDetail.Detail=MenuInfoData[0].Option[ind].Detail

            if(MenuInfoData[0].Option[ind].OptionOfOption === 0){
                setUserChoiceCheck((UserChoiceCheck) => [...UserChoiceCheck, MenuInfoData[0].Option[ind].Detail[0].id]);
            }
            setCreateOption((CreateOption) => [...CreateOption, CreateOptionDetail]);
            CreateOption.Option=CreateOptionDetail
        }
    }, []);






    if (loading) return <div>로딩중..</div>;
    if (error) return <div>에러가 발생했습니다</div>;
    if (!data) return null;

    console.log(state)


    // 더미데이터
    const MenuInfoData = [
        {
          index: 0,
          Basket:2,
          MainDish:0,
          StoreName: '아몬드 가게',
          ImgSrc:"../common/images/tump/img_product_detail.jpg",
          MenuName:'허니버터아몬드',
          MenuContents:'',
          Price:6800,
          Option:[
            {
                index:1,
                OptionSub:'사이즈 선택',
                OptionOfOption:0,
                Detail:[
                    {
                        id:'option4',
                        Detail1:'L',
                        Detail2:'0원',
                        Detail3:0
                    },
                    {
                        id:'option3',
                        Detail1:'M',
                        Detail2:'-5000원',
                        Detail3:-5000
                    },
                    {
                        id:'option5',
                        Detail1:'XL',
                        Detail2:'+5000원',
                        Detail3:5000
                    },
                    {
                        id:'option6',
                        Detail1:'XXL',
                        Detail2:'+10000원',
                        Detail3:10000
                    }
                ]
            },
            {    
                index:2,
                OptionSub:'추가 선택(선택)',
                OptionOfOption:1,
                Detail:[
                    {
                        id:'option1',
                        Detail1:'와사비맛 한봉지 더',
                        Detail2:'+3000원',
                        Detail3:3000
                    },
                    {
                        id:'option2',
                        Detail1:'허니버터맛 한봉지 더',
                        Detail2:'+5000원',
                        Detail3:5000
                    }
                ]              
            }
          ],
          MinAmount:20000
        }
    ]

    

    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a href="#" className="icon pageBack">Back</a>
                    </div>            
                    <div className="middleArea">
                        <h1 className="headerTitle">{StoreName}</h1>
                    </div>
                    <div className="rightArea">
                        <a href="#" className="icon orderCart">
                            장바구니
                            <span className="cartNum">{Basket}</span>
                        </a>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        {       
                            function () {
                                if(MainDish === 1) {
                                    return (
                                        <div className="menuDetailView">
                                            <div className="imgView">
                                                <img src={ImgSrc} />
                                            </div>
                                            <h2 className="title">{MenuName}</h2>
                                            <p className="desc">{MenuContents}</p>
                                        </div>
                                    )
                                } else {
                                    return (
                                        <div className="menuDetailView">
                                            <h2 className="title">{MenuName}</h2>
                                        </div>
                                    )
                                }
                            } ()
                        }
                        <div className="sectionBlock"></div>
                        <div className="menuOrder">
                            <p className="orderPrice flowList">
                                <span className="infoItem">가격</span>
                                <span className="infoDetail">{Price}원</span>
                            </p>
                            <div>
                                <ul className="payList">
                                    {
                                        CreateOption.map((option) => (
                                            function () {
                                                if(option.OptionOfOption === 1) return (
                                                    <MenuOptionCheck OptionSub={option.OptionSub} Detail={option.Detail} 
                                                        checkOption={checkOption}/>
                                                ) 
                                                else return (
                                                    <MenuOptionRadio OptionSub={option.OptionSub} 
                                                        Detail={option.Detail}
                                                        radioOption={radioOption} DefaultOptionId={option.defaultOptionId}
                                                        radioIndex = {option.radioIndex}/>
                                                )
                                            } ()
                                        ))
                                    }
                                </ul>
                            </div>
                            <div className="orderAmount flowList">
                                <p className="infoItem">수량</p>
                                <span className="infoDetail">
                                    <span className="quantity">
                                        <button type="button" className="btn btnMinus" onClick={btnMinus}>빼기</button>
                                        <span className="number">{TotalCount}</span>
                                        <button type="button" className="btn btnPlus" onClick={btnPlus}>더하기</button>
                                    </span>
                                </span>
                            </div>
                        </div>
                        <TotalAmount TotalPrice={TotalPrice} MinAmount={MinAmount}/>
                    </div>
                    <div className="fixedBtn">
                        <a href="#" className="btn addCart">장바구니</a>
                        <a href="#" className="btn addOrder" onClick={btnOrder}>바로주문</a>
                    </div>
                </div>
            </div>
        </div>
    );

    function btnMinus() {
        if(TotalCount - 1 < 1) {
            setTotalCount(TotalCount => 1)
        } else {
            setTotalCount(TotalCount - 1)
        }
    }

    function btnPlus() {
        setTotalCount(TotalCount + 1)
    }

    function btnOrder() {
        console.log(UserChoiceCheck)
    }

    function checkOption(id, TF) {

        setTotalPrice(MenuInfoData[0].Price);

        if(TF === true) {
            setUserChoiceCheck((UserChoiceCheck) => [...UserChoiceCheck, id]);
        }
        else {
            setUserChoiceCheck(UserChoiceCheck.filter((item) => item !== id))
        }
    }

    function radioOption(oldId, newId) {

        CreateOption.map((CreateOptionData) => {
            setTotalPrice(MenuInfoData[0].Price);
            if(CreateOptionData.defaultOptionId === oldId) {
                CreateOptionData.defaultOptionId=newId
                setUserChoiceCheck(UserChoiceCheck.filter((item) => item !== oldId))
                setUserChoiceCheck((UserChoiceCheck) => [...UserChoiceCheck, newId]);
            }
        })
    }
};

const TotalAmount = (props) => {

    return (
        <div className="flowList totalAmount">
            <p className="infoItem">총 주문금액</p>
            <p className="infoDetail ">
                <span className="total">{props.TotalPrice}원</span>
                <span  className="amoutInfo">최소주문금액 {props.MinAmount}원</span>
            </p>
        </div>
    );

}

const MenuOptionRadio = (props) => {
    return (
        <li>
            <p className="title">{props.OptionSub}</p>
            <ul className="flowList">
                {
                    props.Detail.map((detail, index) => (
                        <>
                        <li key={detail.id}>
                            <span className="infoItem">
                                <label className="checkSelect">
                                    <input type="radio" id={detail.id} name={"option" + props.radioIndex} 
                                        onChange={radioButtonOnChange}
                                        checked={props.DefaultOptionId === detail.id}/>
                                    <span className="dCheckBox">{detail.Detail1}</span>
                                </label>
                            </span>
                            <span className="infoDetail">{detail.Detail2}</span>
                        </li>
                        </>
                    ))
                }
            </ul>
        </li>
    );

    function radioButtonOnChange(e) {
        props.radioOption(props.DefaultOptionId, e.currentTarget.id)
    }
}

const MenuOptionCheck = (props) => {

    return (
        <li>
            <p className="title">{props.OptionSub}</p>
            <ul className="flowList">
                {
                    props.Detail.map((detail) => (
                        <li>
                            <span className="infoItem">
                                <label className="checkSelect">
                                    <input type="checkbox" id={detail.id} onChange={checkBoxOnChange} value={detail.Detail3}/> <span className="dCheckBox">{detail.Detail1}</span>
                                </label>
                            </span>
                            <span className="infoDetail">{detail.Detail2}</span>
                        </li>
                    ))
                }
            </ul>
        </li>
    );
    
    function checkBoxOnChange(e) {
        props.checkOption(e.currentTarget.id, e.currentTarget.checked)
    }
}

export default MenuComponent;